* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100dvh;
  display: flex;
}

:root {
  --seconds-animation-time: 60s;
  --minutes-animation-step: 60;
  --minutes-animation-time: calc(var(--seconds-animation-time) * var(--minutes-animation-step));
}

.stopwatch {
  aspect-ratio: 1;
  border: 1vmin dotted #000;
  border-radius: 100dvw;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  display: flex;
}

.stopwatch--speed-up {
  --seconds-animation-time: 10s;
  --minutes-animation-step: 60;
  --minutes-animation-time: calc(var(--seconds-animation-time) * var(--minutes-animation-step));
}

.stopwatch__center {
  aspect-ratio: 1;
  z-index: 5;
  background-color: #f6a603;
  border-radius: 100dvw;
  width: 5vmin;
}

.stopwatch__minutes {
  animation: hand-move var(--minutes-animation-time) steps(var(--minutes-animation-step)) infinite;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch__seconds {
  animation: hand-move var(--seconds-animation-time) linear infinite;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  transform: translateY(-50%);
}

@keyframes hand-move {
  from {
    rotate: 0;
  }

  to {
    rotate: 360deg;
  }
}
